  <div class="full pa-8 fill-height">
    <v-row>
      <v-col>

        <div class="top">
          <v-expansion-panels popout>
            <v-expansion-panel>
              <v-expansion-panel-header class="text-center" style="flex-direction: column;">
                <h1>Filters</h1>
              </v-expansion-panel-header>
              <v-expansion-panel-content>
                <v-tabs fixed-tabs v-model="tabs">
                  <v-tabs-slider></v-tabs-slider>
                  <v-tab key="stage">
                    {{ $t("RFP.stage.name") }}
                  </v-tab>
                  <v-tab key="video">
                    {{ $t("RFP.video.name") }}
                  </v-tab>
                  <v-tab key="operators">
                    {{ $t("RFP.operators.name") }}
                  </v-tab>
                </v-tabs>
                <v-divider/>
                <v-tabs-items v-model="tabs">
                  <v-tab-item key="stage">
                    <stateSelector :info="true" :selectedStage.sync="selectedState"></stateSelector>
                  </v-tab-item>
                  <v-tab-item key="video">
                    video
                  </v-tab-item>
                  <v-tab-item key="operators">
                    operators
                  </v-tab-item>
                </v-tabs-items>
              </v-expansion-panel-content>
            </v-expansion-panel>
          </v-expansion-panels>
        </div>
      </v-col>
    </v-row>

    <v-row>
      <v-col>
        <v-btn @click="getVideos" x-large style="width: 100%" color="success" :disabled="selectedState === null || selectedState === '' || getting">
          <h1>
            GET Result
          </h1>
        </v-btn>
      </v-col>
    </v-row>
    <v-row>
      <v-col>

        <v-card class="pa-5">
          <v-card-title>
            <h1>
              Result
            </h1>
          </v-card-title>
          <v-divider class="pa-3"/>
          <v-card-text>
            <v-data-table :headers="headers" :items="videos">

            </v-data-table>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>


    <v-dialog v-model="addVideoDialog">
      <addVideo></addVideo>
    </v-dialog>
    <v-btn
        elevation="2"
        fab
        style="position: fixed; bottom: 1em; right: 1em"
        @click="addVideoDialog=true"
        color="success"
    >
      <v-icon>
        mdi-plus
      </v-icon>
    </v-btn>
  </div>
