<script>
import stateSelector from "@/components/RaidersFinder/stateSelector";
import addVideo from "@/pages/addVideo.vue";
import axios from "axios";


export default {
  name: "RaidersFinder",
  data() {
    return {
      tabs: null,
      selectedState: null,
      addVideoDialog: false,
      headers: [
        {text: "ID", value: "_id"},
        {text: "Title", value: "title"}
      ],
      videos: [],
      getting: false
    }
  },
  components: {
    stateSelector,
    addVideo
  },
  methods: {
    getVideos: async function () {
      this.getting = true
      await axios.get(`https://white-arknights-tool-back-end.vercel.app/data/NPIuhndslAIUSHGE:OUGNsEDJGBv/${this.selectedState}`).then(data => {
        console.log(data.data)
        this.videos = data["data"]
        this.getting = false
      })
    },
  }
}
</script>

